<template>
  <div class="nav" style="padding-top: 0">
    <a @click="act('jb')" :class="{'active':isAct['jb']}">基本</a>
    <a @click="act('ds')" :class="{'active':isAct['ds']}">导数</a>
    <a @click="act('jf')" :class="{'active':isAct['jf']}">积分</a>
    <a @click="act('sj')" :class="{'active':isAct['sj']}">三角</a>
  </div>
  <div style="font-size: 30px;width: 100%;height: calc(100% - 160px);overflow: scroll;-webkit-overflow-scrolling: touch;"  @touchstart="show=true" @touchend="show=false" >

    <div v-if="isAct['jb']" style="font-size: 20px">
      <template  v-for="(item,key) in jiben"  :key="key">
        <katex  :math="item"></katex>
        <br>
      </template>
    </div>
    <div v-if="isAct['ds']" style="font-size: 20px">
      <template  v-for="(item,key) in daoshu"  :key="key">
        <katex  :math="item"></katex>
        <br>
      </template>
    </div>

    <div v-if="isAct['jf']" style="font-size: 20px">
      <template  v-for="(item,key) in jifen"  :key="key">
        <katex  :math="item"></katex>
        <br>
      </template>
    </div>

    <div v-if="isAct['sj']" style="font-size: 20px">
      <template  v-for="(item,key) in sanjiao"  :key="key">
        <katex  :math="item"></katex>
        <br>
      </template>
    </div>
  </div>
</template>
<script>
import Katex from "@/components/Katex";

export default {
  components: {Katex},
  data:()=>{
    return{
      show:false,
      i:0,
      isAct: {
        ds:true,
        jf:false,
        sj:false
      },
      jiben:[
        'x=\\frac{-b\\pm\\sqrt{b^2-4ac}}{2a}'
      ],
      sanjiao:[
        '1+\\tan ^{2}\\alpha =\\sec ^{2}\\alpha ',
        '1+\\cot ^{2}\\alpha =\\csc ^{2}\\alpha ',
        '\\sin ^{2}\\alpha =\\dfrac{1-\\cos _{2}\\alpha }{2}',
        '\\cos ^{2}\\alpha =\\dfrac{1+\\cos _{2}\\alpha }{2}',
        '\\sin 2\\alpha =2\\sin \\alpha \\cos \\alpha ',
        '\\cos 2\\alpha =\\cos ^{2}\\alpha -\\sin ^{2}\\alpha ',
        '\\tan 2\\alpha =-\\dfrac{2\\tan \\alpha }{1-\\tan ^{2}\\alpha }'
      ],
      daoshu:[
        '\\left( x^{\\alpha }\\right) \'=\\alpha x^{\\alpha -1}',
        '\\left( a^{x}\\right) \'=a^{x}\\ln a',
        '\\left( e^{x}\\right) \'=e^{x}',
        '\\left( \\log _{a}x\\right) \'=\\dfrac{1}{x\\ln a}',
        '\\left( \\ln x\\right) \'=\\dfrac{1}{x}',
        '\\left( \\sin x\\right) \'=\\cos x',
        '\\left( \\cos x\\right) \'=-\\sin x',
        '\\left( \\tan x\\right) \'=\\sec ^{2}x',
        '\\left( \\cot x\\right) \'=-\\csc ^{2}x',
        '\\left( \\sec x\\right) \'=\\sec x\\tan x',
        '\\left( \\csc x\\right) \'=-\\csc x\\cot x',
        '\\left( \\arcsin x\\right) \'=\\dfrac{1}{\\sqrt{1-x^{2}}}',
        '\\left( \\arccos x\\right) \'=-\\dfrac{1}{\\sqrt{1-x^{2}}}',
        '\\left( \\arctan x\\right) \'=\\dfrac{1}{1+x^{2}}',
        '\\left( \\arccot x\\right) \'=-\\dfrac{1}{1+x^{2}}',
      ],
      jifen: [
        "\\int x^{\\alpha }\\operatorname dx=\\dfrac{1}{\\alpha +1}x^{\\alpha +1}+C",
        "\\int \\dfrac{1}{x}\\operatorname dx=\\ln\\left| x\\right| +C",
        "\\int a^{x}\\operatorname dx=\\dfrac{a^{x}}{\\ln a} +C",
        "\\int e^{x}\\operatorname dx=e^{x}+C",
        "\\int \\sin x\\operatorname dx=-\\cos x+C",
        "\\int \\cos x\\operatorname dx=\\sin x+C",
        "\\int \\sec ^{2}x\\operatorname dx=\\tan x+C",
        "\\int \\csc ^{2}x\\operatorname dx=-\\cot x+C",
        "\\int \\sec x\\tan x\\operatorname dx=\\sec x+C",
        "\\int \\csc x\\cot x\\operatorname dx=-\\csc x+C",
        "\\int \\dfrac{1}{\\sqrt{1-x^{2}}}\\operatorname dx=\\arcsin x+C",
        "\\int \\dfrac{\\operatorname dx}{1+x^{2}}=\\arctan x+C",
        "\\int \\dfrac{\\operatorname dx}{\\sqrt{a^{2}-x^{2}}}=\\arcsin \\dfrac{x}{a}+C",
        "\\int \\dfrac{\\operatorname dx}{a^{2}+x^{2}}=\\dfrac{1}{a}\\arctan \\dfrac{x}{a}+C",
        "\\int \\dfrac{\\operatorname dx}{x^{2}-a^{2}}=\\dfrac{1}{2a}\\ln \\left| \\dfrac{x-a}{x+a}\\right| +C",
        "\\int \\dfrac{\\operatorname dx}{\\sqrt{x^{2}+a^{2}}}=\\ln \\left( x+\\sqrt{x^{2}+a^{2}}\\right) +C",
        "\\int \\dfrac{\\operatorname dx}{\\sqrt{x^{2}-a^{2}}}=\\ln \\left| x+\\sqrt{x^{2}-a^{2}}\\right| +C",
        "\\int \\sec x\\operatorname dx=\\ln \\left| \\sec x+\\tan x\\right| +C",
        "\\int \\csc x\\operatorname dx=-\\ln \\left| \\csc x+\\cot x\\right| +C"
      ],
    }
  },
  computed:{
    current(){
      let x=this.daoshu[this.i].split('=')
      return {
        a:x[0],
        b:x[1]
      }
    }
  },
  methods:{
    act(name){
      console.log('act:'+name)
      for (const actKey in this.isAct) {
        this.isAct[actKey]=false
      }

      this.isAct[name]=true
    },
    rand(){
      this.show=false
      let i
      do {
        i=Math.floor((Math.random()*this.daoshu.length))
      }while (i===this.i)
      this.i=i
    },
    next(){
      this.show=false
      this.i=(this.i+1)%this.daoshu.length
    }
  }
}
</script>
